@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

html {
  box-sizing: border-box;
  font-size: 16px;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

body {
    font-family: "Ubuntu", sans-serif;
}

section.TicketHeader {
    display: flex;
    width: 100%;
    justify-content: space-between;
}

fieldset {
    border: transparent;
}

div.DateInput {
    display: flex;
    width: 100%;
    justify-content: flex-end;
}

.InfoInput,
.ItemsTable,
.NotesInput {
    margin: 1rem 0 1rem 0;
    padding: 1rem;
    box-shadow: 0px 0px 3px 2px rgba(0, 0, 0, 0.2);
}

.InfoInput div {
    display: flex;
    width: 100%;
    flex-direction: column;
    grid-gap: 0.2rem;
    gap: 0.2rem;
    margin: 1rem 0 0 0;
}

.InfoInput-header,
.NotesInput-header {
    color: #274ae5;
}

.InfoInput input {
    border: 3px solid rgba(0, 0, 0, 0.2);
}

.table-header-container,
.ItemsInput {
    display: flex;
    width: 100%;
}
.table-header {
    width: 100%;
    font-weight: bold;
}

.ItemsInput {
    margin: 1rem 0 1rem 0;
    grid-gap: 0.2rem;
    gap: 0.2rem;
}

.ItemsInput input {
    width: 100%;
}

.item-results {
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.ItemsTotals div {
    display: flex;
    width: 100%;
    justify-content: flex-end;
    grid-gap: 1rem;
    gap: 1rem;
    font-size: 1.5rem;
}

.ItemsTotals div h5 {
    font-weight: bold;
}

.NotesInput {
    display: flex;
    flex-direction: column;
}

.SubmitButtons {
    display: flex;
    justify-content: space-around;
}

/* Buttons */
.btn {
    border: transparent;
    border-radius: 3px;
    padding: 0.3rem;
    color: #000;
    font-weight: normal;
}
.add-item {
    background-color: rgb(120 195 8);
    max-height: 2rem;
    width: 2rem;
}

.delete-item {
    background-color: rgb(251 91 91);
    width: 25px;
    font-weight: bold;
}

.submit-btn {
    background-color: #274ae5;
    color: #fff;
    font-weight: bold;
    border-radius: 1px;
    min-width: 80px;
}

/* Animations */
.fade-in {
    opacity: 1;
    -webkit-animation-name: fadeInOpacity;
            animation-name: fadeInOpacity;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
}

@-webkit-keyframes fadeInOpacity {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeInOpacity {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.fade-out {
    opacity: 0;
    -webkit-animation-name: fadeOutOpacity;
            animation-name: fadeOutOpacity;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-duration: 200ms;
            animation-duration: 200ms;
}
@-webkit-keyframes fadeOutOpacity {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
@keyframes fadeOutOpacity {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

.version {
    font-size: 1rem;
    color: rgba(185, 178, 178, 0.8);
    align-self: center;
    text-align: center;
}

